const {gamestate} = require('./gamestate');


export class Room {
    constructor(id){
        this.id = id;
        this.connections = [];
        this.entered = false;
    }

    connectRoom(room){
        if(this.connections.indexOf(room) == -1){
            this.connections.push(room);
            room.connectRoom(this);
        }
    }

    enter(terminal, prev){
        console.log(`Entered room ${this.id}. I have entered before: ${this.entered}. Connections: ${this.connections.map(c => c.id)}`);

        //terminal.clear();

        terminal.writeln('');
        terminal.writeln('');

        // if(prev != null && this.entered && prev.id === this.id){
        //     this.writeDescription(terminal);
        // }
        if(!this.entered){
            this.entered = true;
            this.writeEnterText(terminal);
            terminal.writeln('');
            terminal.writeln('');
            this.writeDescription(terminal);
        }
        terminal.writeln('');
        terminal.writeln('');
        terminal.writeln('Vad gör du?');
        terminal.writeln('');
        this.writeActions(terminal);
    }

    writeDescription(terminal){
        terminal.writeln('Ett helt kalt rum. Läskigt!');
    }

    writeEnterText(terminal){
        terminal.writeln('Ett helt kalt rum. Läskigt!');
    }

    writeActions(terminal){

    }

    takeAction(actionId, terminal){
        return this;
    }

    
}

export class Bedroom extends Room{
    constructor(){
        super('bedroom');
    }

    writeDescription(terminal){
        terminal.writeln('Du befinner dig i ett sovrum');
        terminal.writeln('Dom mörka gardinerna är fördragna men en tunn ljusstrimma syns där tyget möter väggen');
        terminal.writeln('Mitt i rummet står en stor säng med senapsgula sängkläder');
        terminal.writeln('På en byrå vid fotändan av sängen står en tårta. De 25 ljusen verkar ha precis blivt utblåsta');
        terminal.writeln('En stor palm står ståtlig på en blå pall i hörnet av rummet');
        terminal.writeln('Bredvid sängen står en röd fåtölj. Diverse kläder står runt ');
        terminal.writeln('En dörr in till nästa rum står lite på glänt');
    }

    writeEnterText(terminal){
        terminal.writeln('Efter en god natts sömn vaknar du på denna speciella dag av skönsång.');
        terminal.writeln('Rummet är fylld av en orange glöd av 25(!) ljus på en tårta.');
        terminal.writeln('Du blåser ut ljusen och känner lukten av rök och stearin');
    }

    writeActions(terminal){
        if(!gamestate.book){
            terminal.writeln('1. Titta närmare på tårtan');
        }
        terminal.writeln('2. Öppna dörren som står lite på glänt och gå ut ur den');

    }

    takeAction(actionId, terminal){
        switch(actionId){
            case 1:
                terminal.writeln('När du tittar närmare på tårtan känner du doften av citroner. Röda fina jordgubbar sitter på toppen.');
                terminal.writeln('Lite bakom tårtan ligger en bok. När du tittar närmare på den så ser du att den innehåller en massa förslag på äventyr');
                terminal.writeln('Men för att åka på sånna äventyr behövs ju lite utrustning...');
                gamestate.book = true;
                return this;
            case 2:
                terminal.writeln('Du öppnar dörren som stod lite på glänt...');
                return this.connections[0];
            default:
                terminal.writeln('Det är svårt att bestämma sig när man är nyvaken. Du bestämmer dig för att tänka igenom allt lite igen...');
                return this;
        }
    }
}

export class LivingRoom extends Room{


    constructor(){
        super('livingroom');

    }

    writeDescription(terminal){
        terminal.writeln('Du befinner dig i ett vardagsrum');
        terminal.writeln('En stor mysig soffa står längs väggen med ett soffbord av trä framför sig. ');
        terminal.writeln('I fönstret står en massa växter. Dom ger rummet en härlig känsla av liv och färg.');
        terminal.writeln('Bredvid soffan står ett runt bord med 4 gröna stolar runt sig.');
        if(!gamestate.clothes){
            terminal.writeln('På bordet ligger ett paket inslaget med grönt papper. Vad kan det vara?');
        }
        else{
            terminal.writeln('På bordet ligger grönt omslagspapper i en liten hög.');
        }
        terminal.writeln('Du ser en dörröppning in till ett kök');
        terminal.writeln('Bredvid denna finns en glasdörr. Genom fönstret ser du en hall');
        terminal.writeln('Mitt emot soffan finns en dörr in till sovrummet')
    }

    writeEnterText(terminal){
        terminal.writeln('Detta rummet är mycket ljusare');
        terminal.writeln('Du känner hur du vaknar till');
        terminal.writeln('Någonting mjukt stryker sig förbi ditt ben. En lite katt! Katten tittar på dig och buffar lite lätt med huvudet mot ditt ben');
    }

    writeActions(terminal){
        if(!gamestate.clothes){
            terminal.writeln('1. Öppna paketet på bordet');
        }
        terminal.writeln('2. Gå in i köket');
        terminal.writeln('3. Gå in i hallen');
        terminal.writeln('4. Gå in i sovrummet');
        terminal.writeln('5. Klappa den lilla katten');
    }

    takeAction(actionId, terminal){
        switch(actionId){
            case 1:
                terminal.writeln('Med ivriga fingrar sliter du upp presentpappret, som flyger åt alla håll.')
                terminal.writeln('Du ser en jacka och ett par vandrings byxor. Jackan ser vattentät ut, men att den verkar ändå andas och den är precis i den färgen du gillar');
                terminal.writeln('Byxorna ser perfekta ut för att gå runt i skogsdungar. De har till och med lite extra fickor')
                terminal.writeln('När du tar på dig kläderna känner du dig lite mer redo för att ta dig ut på äventyr...');
                gamestate.clothes = true;
                return this;
            case 2:
                terminal.writeln('Nyfiket går du in i köket...');
                return this.connections[0];
            case 3:
                terminal.writeln('Med bestämda steg går du fram och öppnar dörren in till hallen...');
                return this.connections[1];
            case 4:
                terminal.writeln('Sängen lockar, du går in i sovrummet...');
                return this.connections[2];
            case 5:
                terminal.writeln('Den lilla katten stryker sig mot din utsträkta hand. Efter ett liten stund hör du en svagt spinnande från hennes mage');
                return this;
            default:
                terminal.writeln('Det är svårt att bestämma sig när man är nyvaken. Du bestämmer dig för att tänka igenom allt lite igen');
                return this;
        }
    }
}

export class Kitchen extends Room{
    constructor(){
        super('kitchen');
    }

    writeDescription(terminal){
        terminal.writeln('Du befinner dig i ett kök');
        if(!gamestate.breakfast){
            terminal.writeln('På köksbänken står det mat uppdukat.');
            terminal.writeln('Yoghurt, bröd, ost, smör och annat pålägg.');
            terminal.writeln('Te står i en liten terrakotta kanna');
            terminal.writeln('Två koppar och två små tallrikar står redan framme');
        }
        else{
            terminal.writeln('Resterna av den goda frukosten står fortarande framme.');
            terminal.writeln('Hoppas någon diskar');
        }
    }

    writeEnterText(terminal){
        terminal.writeln('Innan man kan ge sig ut på äventyr måste man ha frukost tänker du.');
        terminal.writeln('Men vad ska man äta?')
    }

    writeActions(terminal){
        if(!gamestate.breakfast){
            terminal.writeln('1. Ät frukost');
        }
        terminal.writeln('2. Gå tillbaka in i vardagsrummet');
    }

    takeAction(actionId, terminal){
        switch(actionId){
            case 1:
                terminal.writeln('Frukosten sitter extra bra när man vet att det finns tårta efteråt. Den sker ju också i gott sällskap');
                gamestate.breakfast = true;
                return this;
            case 2:
                terminal.writeln('Vad mer kan det finnas i lägenheten? Du går in i vardagsrummet igen');
                return this.connections[0];
            default:
                terminal.writeln('Det är svårt att bestämma sig när man är nyvaken. Du bestämmer dig för att tänka igenom allt lite igen...');
                return this;
        }
    }
}

export class Hallway extends Room{
    constructor(){
        super('hallway');
    }

    writeDescription(terminal){
        terminal.writeln('Du befinner dig i hallen');
        terminal.writeln('Garderober fyllda med saker finns på höger sida');
        terminal.writeln('Framför dig ser du en ytterdörr. Är det dags att gå ut på äventyr?');
    }

    writeEnterText(terminal){
        terminal.writeln('En fin dag som denna måste man ju ut tänker du när du går in i hallen')
    }

    writeActions(terminal){
        terminal.writeln('1. Gå ut genom ytterdörren');
        terminal.writeln('2. Gå tillbaka in i vardagsrummet');
    }

    takeAction(actionId, terminal){
        switch(actionId){
            case 1:
                if(!gamestate.clothes){
                    terminal.writeln('Innan man går på äventyr behövs ju lite kläder! Nog bäst att fixa det först');
                    return this;
                }

                if(!gamestate.breakfast){
                    terminal.writeln('Precis innan du ska gå ut genom dörren kurrar magen till. Lite frukost skulle sitta fint, tänker du.');
                    return this;
                }

                if(!gamestate.book){
                    terminal.writeln('Vad ska du göra där ute? Det hade varit bra med en karta eller två först');
                    return this;
                }

                terminal.writeln('Helt kittad och med mat i magen öppnar du ytterdörren. Tänk vad roligt man kan ha det ute i naturen.');
                return this.connections[0];
            case 2:
                terminal.writeln('Inte riktigt dags för äventyr än. Du går in i vardagsrummet igen...');
                return this.connections[1];
            default:
                terminal.writeln('Det är svårt att bestämma sig när man är nyvaken. Du bestämmer dig för att tänka igenom allt lite igen...');
                return this;
        }
    }
}

