
require('xterm/css/xterm.css');
const { Terminal } = require('xterm');
const {initializeRooms} = require('./rooms');
document.addEventListener("DOMContentLoaded", () => {
    var term = new Terminal({cols: 150, fontSize: 18, cursorBlink: true});
    term.open(document.getElementById('terminal'));
    
    function runFakeTerminal() {
        if (term._initialized) {
            return;
        }
        const rooms = initializeRooms();

        var currentRoom = rooms.bedroom;
        term._initialized = true;

        term.prompt = () => {
            term.write('\r\n> ');
        };

        term.writeln('Välkommen till sophias födelsedag!');
        term.writeln('');
        var nextLine = '';
        term.onKey(e => {
            const printable = !e.domEvent.altKey && !e.domEvent.altGraphKey && !e.domEvent.ctrlKey && !e.domEvent.metaKey;

            if (e.domEvent.keyCode === 13) {
                term.writeln('');
                const action = Number(nextLine);
                console.log(`Got ${nextLine} - Action: ${action}`);
                let nextRoom = currentRoom.takeAction(action, term);
                nextRoom.enter(term, currentRoom);
                currentRoom = nextRoom;
                nextLine = '';
                prompt(term);
            } else if (e.domEvent.keyCode === 8) {
                // Do not delete the prompt
                if (term._core.buffer.x > 2) {
                    term.write('\b \b');
                }
            } else if (printable) {
                term.write(e.key);
                nextLine += e.key;
            }
        });

        currentRoom.enter(term, null);
        prompt(term);
    }

    function prompt(term) {
        term.write('\r\n$ ');
    }

    runFakeTerminal();

});
